$breakpoint: (
  'small': 36rem,
  'medium': 50rem,
  'large': 64rem
);


@mixin respond($bp) {
  @media (min-width: map-get($breakpoint, $bp)) {
    @content;
  }
}

.test-2 {
  width: 100%;

  @include respond('medium') {
    width: 40em;
  }
}

